import React from 'react'
import Link from 'gatsby-link'
import get from 'lodash/get'
import Helmet from 'react-helmet'


// import Lightbox from 'react-images'
import Gallery from "../components/Gallery"

import thumb01 from '../assets/images/thumbs_ML/bolsas_.jpg'
import thumb02 from '../assets/images/thumbs_ML/bolsas-2.jpg'
import thumb03 from '../assets/images/thumbs_ML/bolsas-3.jpg'


import full01 from '../assets/images/fulls/bolsas_.jpg'
import full02 from '../assets/images/fulls/bolsas-2.jpg'
import full03 from '../assets/images/fulls/bolsas-3.jpg'


const DEFAULT_IMAGES = [
    { id: '1', src: full01, thumbnail: thumb01, caption: 'Photo 1', description: 'Set De Jard√≠n De Infantes Personalizado En Quilmes Cole Kit'},
    { id: '2', src: full02, thumbnail: thumb02, caption: 'Photo 2', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'},
    { id: '3', src: full03, thumbnail: thumb03, caption: 'Photo 3', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'}
];

class HomeIndex extends React.Component {

    

    render() {
        //const siteTitle = this.props.data.site.siteMetadata.title
        //const siteDescription = this.props.data.site.siteMetadata.description

        return (
            <div>
                <Helmet>
                        <title>hi</title>
                        <meta name="description" />
                </Helmet>

                <div id="main">

                    <section id="one">
                        <header>
                            <h2>Bolsas Para Chicos (ideal Jard√≠n) 29 X 33cm (aprox)</h2>
                            <h4>$65</h4> 
                        </header>
                        <p>DESCRIPCI√ìN: <br />
                        Bolsas de jardin 29 x 33cm (medidas aproximadas)<br /> 
                        * Material: tela impermeable.<br /> 
                        * Se pueden personalizar con cualquier imagen o foto.<br /> 
                        </p>
                       
                       <a mp-mode="dftl" href="https://www.mercadopago.com/mla/checkout/start?pref_id=8068205-0f335b36-da45-457e-9eec-9bdc9b420ea6" name="MP-payButton" className='blue-ar-l-rn-none preview__button'>Pagar</a><br /><br /> 
                        <Link to="/">Volver</Link>

                        <br /><br /> 
                    </section>
                
                        <h2></h2>
                    
                        <Gallery images={DEFAULT_IMAGES.map(({ id, src, thumbnail, caption, description }) => ({
                            src,
                            thumbnail
                        }))} />

                        
                    

                </div>

            </div>
        )
    }
}

export default HomeIndex

